import { Component, OnInit, ViewChild } from '@angular/core';
import { NotifyManagementService } from './notify-management.service';
import { PopoverController } from '@ionic/angular';
import { UtilsService } from 'src/app/services/utils.service';
import { AppValueService } from 'src/app/services/app-value.service';
import { TranslateService } from '@ngx-translate/core';
import { ModalEditNotifyComponent } from 'src/app/components/modal-edit-notify/modal-edit-notify.component';

@Component({
  selector: 'app-notify-management',
  templateUrl: './notify-management.page.html',
  styleUrls: ['./notify-management.page.scss'],
})
export class NotifyManagementPage implements OnInit {
  @ViewChild('contentSearch') contentSearch: any;  
  constructor(private popoverController: PopoverController,
    public notifyService: NotifyManagementService,
    public utilsService: UtilsService,
    public appValueService: AppValueService,
    private translate: TranslateService) { }

  ngOnInit() {
  }
  showDetail(data: any, mode: any) {
    if(mode == "edit"){
      this.showModalEditNotify(data.ev, data.row._row.data);
    }
    else {
      this.showModalEditNotify(null, null);
    }    
  }

  async showModalEditNotify(ev: any, row: any) {
    const popover = await this.popoverController.create({
      component: ModalEditNotifyComponent,
      id: "editNotifyModal",
      event: ev,
      translucent: true,
      componentProps: { rowProduct: row },
      cssClass: "edit-notify-popover"
    });
    return await popover.present();
  }
  ionViewDidEnter() {
    let removeHeight = [];
    this.appValueService.APP_TABULATOR_HEIGHT = [this.contentSearch, removeHeight];
    this.notifyService.heightTable = this.utilsService.settingTableHeight(this.appValueService.APP_TABULATOR_HEIGHT);
  }
}
